@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?b45lp1');
  src:  url('fonts/icomoon.eot?b45lp1#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?b45lp1') format('truetype'),
    url('fonts/icomoon.woff?b45lp1') format('woff'),
    url('fonts/icomoon.svg?b45lp1#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-facebook:before {
  content: "\e900";
}
.icon-google:before {
  content: "\e901";
}
.icon-visa:before {
  content: "\e902";
}
.icon-whatsapp:before {
  content: "\e903";
}
.icon-react:before {
  content: "\e904";
}
